/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}
html {
    height: 100%;
    background-color: #aae1ff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#root {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.container {
    padding: 3em;
    width: 27em;
}

.search-panel form {
    display: -webkit-flex;
    display: flex;
    font-weight: bold;
    margin-bottom: 1em;
    box-shadow: 0 0.5em 1em 0 rgba(0, 0, 0, 0.1);
}

.search-panel input {
    background: #fff;
    font: inherit;
    border: 0;
    outline: 0;
    padding: 0.5em 1em;
    width: 100%;
}

.search-panel button {
    background: #0A9ABC;
    color: #fff;
    padding: 0.9em 1.5em;
    font: inherit;
    border: 0;
    outline: 0;
    -webkit-transition: all 200ms ease-in;
    transition: all 200ms ease-in;
    cursor: pointer;
}

.search-panel button:hover {
    background: #097994;
}

.weather-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: #EDF2F7;
    box-shadow: 0 0.5em 1em 0 rgba(0, 0, 0, 0.1);
    color: #0A9ABC;
    max-width: 27em;
}

.date-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #FFFFFF;
    padding: 1em;
    color: inherit;
    font-weight: bold;
    font-size: 2em;
}

.weather-conditions {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    padding: 1.5em;
    color: inherit;
}

.weather-conditions>div>div {
    padding: 0.3em;
}

.weather-temperature {
    font-weight: bold;
    font-size: 5em;
}

.error {
    color: #0A9ABC;
    font-weight: bold;
    font-size: 2em;
}

.weather-img>img {
    width: 100%;
    height: 100%;
}

.search-panel__hide-input {
    display: none;
}

.weather-city {
    font-size: 2em;
}
